import { ComponentPropsWithRef, forwardRef } from "react";
import styled, { css } from "styled-components";

export interface FieldLabelProps extends ComponentPropsWithRef<"label"> {
  /**
   * @default false
   * @description show pseudo element (*) with content text when true
   */
  $required?: boolean;
  /**
   * @description content text
   */
  text: string;
  /**
   * @description other children content excludes content text
   * @include phrasing_content [source: mdn] https://developer.mozilla.org/en-US/docs/Web/HTML/Content_categories#phrasing_content, tags like h3 are not allowed.
   */
  children?: React.ReactNode;
}

/** @design https://www.figma.com/design/JGFHYME2p7RpeMYcoMqooS/ODE-Library-1.0?node-id=7966-24427&t=zbZGBrco5ra09mre-4 */
export const FieldLabel = forwardRef<HTMLLabelElement, FieldLabelProps>(
  (props, ref) => {
    const { text, children, ...labelProps } = props;
    return (
      <RawLabel {...labelProps} ref={ref}>
        {!!text && <LabelText>{text}</LabelText>}
        {children}
      </RawLabel>
    );
  }
);

const LabelText = styled.span`
  position: relative;
`;

const RawLabel = styled.label<Omit<FieldLabelProps, "text">>`
  ${(p) => p.theme.fonts["Caption-Large/Bold"]}
  color: ${(p) => p.theme.colors.noColor.Grey14};
  position: relative;
  display: inline-block;

  ${({ $required, theme }) =>
    $required &&
    css`
      ${LabelText} {
        &::before {
          content: "*";
          display: inline-block;
          color: ${theme.colors.baseColor.Volcano5};
          position: absolute;
          top: 0;
          right: -4px;
          transform: translateX(100%);
        }
      }
    `}
`;
